// ** Reactstrap Imports
// ** React Imports
import { useEffect, useState } from 'react'
import Flatpickr from 'react-flatpickr'
import ReactPaginate from 'react-paginate'
// ** Reactstrap Imports
import {
    Row,
    Col,
    Card,
    Form,
    Label,
    Input,
    Button,
    CardBody,
    FormFeedback,
    Table,
    Modal,
    ModalHeader,
    ModalBody
} from 'reactstrap'

import { useForm, Controller } from "react-hook-form"
import * as yup from "yup"
import { yupResolver } from "@hookform/resolvers/yup"
import { activeOrganizationid, orgUserId } from '@src/helper/sassHelper'
import '@styles/react/libs/flatpickr/flatpickr.scss'
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
import axios from '@src/configs/axios/axiosConfig'
const activeOrgId = activeOrganizationid()
const userId = orgUserId()
// ** Third Party Components

import classnames from 'classnames'
import '@styles/react/libs/react-select/_react-select.scss'
import '@styles/react/libs/tables/react-dataTable-component.scss'
import moment from 'moment'
import toast from 'react-hot-toast'
import { Edit, Trash2, Plus, Eye } from 'react-feather'
import { useParams } from 'react-router-dom'

const PasswordManage = () => {

    const MySwal = withReactContent(Swal)
    const [data, setData] = useState([])
    const { id } = useParams()
    const [formModal, setFormModal] = useState(false)
    const [selected, setSelected] = useState(null)
    const [remarks, setRemarks] = useState('')
    const [total, setTotal] = useState(1)
    const [currentPage, setCurrentPage] = useState(1)
    const rowsPerPage = 25
    const [searchvalue, setSearchvalue] = useState('')    
    const [clientId, setClientId] = useState(null)

    const schema = yup.object().shape({
        mastername: yup.string().required("Please Enter Name"),
        passwordlink: yup.string().required("Please Enter Name"),
        username: yup.string().required("Please Enter username"),
        password: yup.string().required("Please Enter Password"),
        id: yup.string(),
        clientid: yup.string().required('Please Select Client').default(id || ''),
        createdby: yup.string().default(userId),
        updatedby: yup.string().default(userId),
        organizationId: yup.string().default(activeOrgId),
        remarks: yup.string().nullable()
    })

    const { handleSubmit, formState: { errors }, control, setValue } = useForm({
        resolver: yupResolver(schema),
        defaultValues: schema.cast()
    })

    const resetForm = () => {
        setValue('mastername', '')
        setValue('passwordlink', '')
        setValue('username', '')
        setValue('remarks', '')
        setValue('password', '')
        setValue('clientId', '')
        setValue('id', '')
        setValue('organizationId', activeOrgId)
        setValue('createdby', userId)
        setValue('updatedby', userId)
        setRemarks('')
    }

    const closeModal = () => {
        setFormModal(false)
        resetForm()
    }

    const openModal = () => {
        setFormModal(true)
        resetForm()
    }

    const openEditModal = card => {
        setSelected(card)
        setFormModal(true)

        setValue('passwordlink', card.passwordlink)
        setValue('mastername', card.mastername)
        setValue('username', card.username)
        setValue('password', card.password)
        setValue('remarks', card.remarks)
        setValue('clientid', card.clientid)
        setValue('id', card.id)
        setValue('organizationId', activeOrgId)
        setValue('createdby', userId)
        setValue('updatedby', userId)
        setRemarks(card.remarks)

    }


    const getLicenses = (params) => {

        axios.post('/passwordmanage/list', params)
            .then((res) => {
                setData(res.data.passwordmanage.passwordmanage)
                setTotal(res.data.passwordmanage.total)
            })
            .catch((err) => { console.log(err) })
    }

    const deletefn = (id) => {
        return MySwal.fire({
            title: 'Are you sure?',
            text: "You won't be able to revert this!",
            icon: 'warning',
            showCancelButton: true,
            confirmButtonText: 'Yes, delete it!',
            customClass: {
                confirmButton: 'btn btn-primary',
                cancelButton: 'btn btn-outline-danger ms-1'
            },
            buttonsStyling: false
        }).then(async (result) => {
            if (result.value) {
                await axios.post('/passwordmanage/delete', { id, updatedBy: userId })
                const params = {
                    clientId,
                    page: currentPage,
                    q: searchvalue
                }
                getLicenses(params)
                MySwal.fire({
                    icon: 'success',
                    title: 'Deleted!',
                    text: 'Document Status has been deleted.',
                    customClass: {
                        confirmButton: 'btn btn-success'
                    }
                })
                return true
            } else if (result.dismiss === MySwal.DismissReason.cancel) {
                return false
            }
        })
    }


    const onSubmit = async data => {
        data.issuedDate = moment(data.issuedDate, "MMM DD, Y").valueOf()
        if (selected !== null) {
            axios.post('/passwordmanage/update', data)
                .then(() => {
                    const params = {
                        clientId,
                        page: currentPage,
                        q: searchvalue
                    }
                    getLicenses(params)
                    setFormModal(false)
                    toast.success('Documents Place Updated Successfully', { position: 'bottom-right' })
                })
                .catch((err) => { console.log(err) })
            setSelected(null)
        } else {
            axios.post('/passwordmanage/create', data)
                .then(async () => {
                    const params = {
                        clientId,
                        page: currentPage,
                        q: searchvalue
                    }

                    getLicenses(params)
                    setFormModal(false)
                    toast.success('Document Place Added Successfully', { position: 'bottom-right' })
                })
                .catch((err) => { console.log(err) })
        }
        closeModal()
    }

    useEffect(() => {
        setClientId(localStorage.getItem('clientId'))
        setValue('clientid', localStorage.getItem('clientId'))
        const params = {
            clientId: localStorage.getItem('clientId'),
            page: currentPage,
            q: searchvalue
        }
        getLicenses(params)
    }, [])

    const handleFilter = (val) => {
        setSearchvalue(val)
        const params = {
            clientId,
            page: currentPage,
            q: val
        }
        getLicenses(params)
    }

    const getRow = (fieldLabel, fieldName, reqflag = true) => {
        return (
            <Col md={12}>
                <Label sm='12' className={classnames(`form-label ${reqflag ? 'required' : ''}`)} for={fieldName}>
                    {fieldLabel}
                </Label>
                <Col>
                    <Controller
                        id={fieldName}
                        name={fieldName}
                        control={control}
                        render={({ field }) => <Input invalid={errors[fieldName] && true} {...field} />}
                    />
                    {errors[fieldName] && <FormFeedback>{errors[fieldName].message}</FormFeedback>}
                </Col>
            </Col>
        )
    }
    const [showModal, setShowModal] = useState(false)
    const openShowModal = (item) => {
        setSelected(item)
        setShowModal(true)
    }

    const handlePagination = (page) => {
        setCurrentPage(page.selected + 1)
        const params = {
            clientId,
            page: page.selected + 1,
            q: searchvalue
        }
        getLicenses(params)
    }

    const CustomPagination = () => {
        const count = Number(Math.ceil(total / rowsPerPage))

        return (
            <ReactPaginate
                previousLabel={''}
                nextLabel={''}
                pageCount={count || 1}
                activeClassName='active'
                forcePage={currentPage !== 0 ? currentPage - 1 : 0}
                onPageChange={page => handlePagination(page)}
                pageClassName={'page-item'}
                nextLinkClassName={'page-link'}
                nextClassName={'page-item next'}
                previousClassName={'page-item prev'}
                previousLinkClassName={'page-link'}
                pageLinkClassName={'page-link'}
                containerClassName={'pagination react-paginate justify-content-end my-2 pe-1'}
            />
        )
    }

    return (
        <Card>
            <CardBody >
                <Row className='d-flex justify-content-between px-1 px-lg-0'>
                    <Col xl='3' md='3' sm='3' className='d-flex align-items-center p-0'>
                        Passwords
                    </Col>
                    <Col className='d-flex align-items-sm-center justify-content-xl-end justify-content-end flex-xl-nowrap flex-wrap flex-sm-row flex-column pe-xl-1 p-0 mt-xl-0 mt-0'>
                        <Col className='col-md-3 me-1 mt-50 mt-sm-0 '>
                            <Input
                                id='search-invoice'
                                className='me-1'
                                type='text'
                                placeholder='Search'
                                value={searchvalue}
                                onChange={e => handleFilter(e.target.value)}
                            />
                        </Col>
                        <Col className='d-flex mt-50 mt-lg-0'>
                            <Button size='sm' className='me-1' color='primary' onClick={() => { openModal() }}>
                                <Plus size={14} />
                                Add New
                            </Button>
                        </Col>
                    </Col>
                </Row>
            </CardBody>
            <Table className='text-nowrap text-center border-bottom' responsive>
                <thead>
                    <tr>                      
                        <th className='text-start p-50'>Name</th>
                        <th>Link</th>
                        <th>username</th>
                        <th>Password</th>
                        <th>Remarks</th>
                        <th>Action</th>
                    </tr>
                </thead>
                <tbody>
                    {data.map((item, index) => {
                        return (
                            <tr key={index}>                                
                                <td className='text-start p-50'>{item.mastername}</td>
                                <td className='text-start p-50'>
                                    <a href={item.passwordlink} target='_blank'>{item.passwordlink?.length > 35 ? `${item.passwordlink?.substr(0, 35)}...` : item.passwordlink}</a>
                                </td>
                                <td className='text-start p-50'>
                                    {item.username?.length > 15 ? `${item.username?.substr(0, 15)}...` : item.username}
                                </td>
                                <td className='text-start p-50'>
                                    {item.password}
                                </td>
                                <td className='text-start p-50'>
                                    <span> {item.remarks?.length > 20 ? `${item.remarks?.substr(0, 20)}...` : item.remarks}</span>
                                </td>
                                <td>
                                    <div className='column-action d-flex align-items-center'>
                                        <Col
                                            onClick={() => { openShowModal(item) }}>
                                            <Eye className='cursor-pointer ms-25 mt-0' size={16} />
                                        </Col>
                                        <Col
                                            onClick={() => { openEditModal(item) }}>
                                            <Edit className='cursor-pointer ms-25 mt-0' size={16} />
                                        </Col>
                                        <Col
                                            onClick={() => { deletefn(item.id) }}>
                                            <Trash2
                                                className='cursor-pointer ms -25 mt-0' size={16} />
                                        </Col>
                                    </div>
                                </td>
                            </tr>
                        )
                    })}
                </tbody>
            </Table>
            <CustomPagination />
            <Modal isOpen={formModal} toggle={() => setFormModal(!formModal)} className='modal-dialog-centered'>
                <ModalHeader toggle={() => closeModal()}>Password Manager</ModalHeader>
                <ModalBody>
                    {
                        Object.keys(errors).map((key) => {

                            return <FormFeedback key={key}>${errors[key]?.message}</FormFeedback>

                        })
                    }
                    <Row tag={Form} className='gx-2 gy-1' onSubmit={handleSubmit(onSubmit)}>


                        {getRow('Name', 'mastername', true)}
                        {getRow('Link', 'passwordlink', true)}

                        {getRow('Login', 'username', true)}
                        {getRow('Password', 'password', true)}
                        <Col md={12}>
                            <Label className='form-label' for='comments'>
                                Remarks
                            </Label>
                            <Controller
                                control={control}
                                name={`remarks`}
                                id='remarks'
                                render={({ }) => (
                                    <Input type='textarea' className='' value={remarks} onChange={(e) => { setValue('remarks', e.target.value); setRemarks(e.target.value) }}
                                    />
                                )}
                            />
                            {errors.documentPlace && <FormFeedback>{errors.documentPlace.message}</FormFeedback>}

                        </Col>

                        <Col className='mt-2 pt-1' xs={12}>
                            <Button type='submit' className='me-1' color='primary'>
                                Submit
                            </Button>
                            <Button className='me-1' color='warning' onClick={() => closeModal()}>
                                Cancel
                            </Button>
                        </Col>

                    </Row>
                </ModalBody>
            </Modal>

            <Modal isOpen={showModal} toggle={() => setShowModal(!showModal)} className='modal-dialog-centered'>
                <ModalHeader toggle={() => setShowModal(false)}>Password Manager</ModalHeader>
                <ModalBody>
                    <Card>
                        <CardBody>
                            <ul className='list-unstyled'>
                                <li className='mb-75 row'>
                                    <span className='fw-bolder col-6'>Name:</span>
                                    <span className='col-6 text-end'>{selected?.mastername}</span>
                                </li>
                                <li className='mb-75 row'>
                                    <span className='fw-bolder col-6'>Link:</span>
                                    <span className='col-6 text-end'>
                                        <a href={selected?.passwordlink} target='_blank'>{selected?.passwordlink}
                                        </a></span>
                                </li>
                                <li className='mb-75 row'>
                                    <span className='fw-bolder col-6'>UserName:</span>
                                    <span className='col-6 text-end'>{selected?.username}</span>
                                </li>
                                <li className='mb-75 row'>
                                    <span className='fw-bolder col-6'>Password:</span>
                                    <span className='col-6 text-end'>{selected?.password}</span>
                                </li>
                                <li className='mb-75 row'>
                                    <span className='fw-bolder col-6'>Remarks:</span>
                                    <p className='col-6 text-end white-space-wrap'>{selected?.remarks}</p>
                                </li>
                            </ul>
                        </CardBody>
                    </Card>
                </ModalBody>
            </Modal>
        </Card>
    )
}

export default PasswordManage
